  padding: 8px 12px;
  display: flex;
  justify-content: space-between;
  align-items: center;
  cursor: grab;
}
.window-titlebar:active {
  cursor: grabbing;
}
.window-buttons {
  display: flex;
  gap: 6px;
}
.window-button {
  width: 12px;
  height: 12px;
  border-radius: 50%;
}
.window-close {
  background: #ff5f56;
}
.window-minimize {
  background: #ffbd2e;
}
.window-maximize {
  background: #27ca40;
}
.window-content {
  padding: 16px;
  color: #e0e0e0;
  max-height: 400px;
  overflow-y: auto;
}
.taskbar {
  position: fixed;
  bottom: 0;
  left: 0;
  right: 0;
  height: 48px;
  background: rgba(26, 26, 46, 0.95);
  border-top: 1px solid #4a4a6a;
  display: flex;
  align-items: center;
  padding: 0 16px;
  backdrop-filter: blur(10px);
}
.start-button {
  background: linear-gradient(135deg, #00aaff, #0066cc);
  padding: 8px 16px;
  border-radius: 4px;
  font-weight: bold;
  cursor: pointer;
  transition: all 0.2s;
}
.start-button:hover {
  background: linear-gradient(135deg, #33bbff, #0077dd);
}
.monitor-container {
  min-height: 100vh;
  display: flex;
  align-items: flex-end;
  justify-content: center;
  background: linear-gradient(180deg, #1a1a2e 0%, #0d0d15 100%);
  padding: 0;
  padding-top: 10px;
}
.monitor-frame {
  background: linear-gradient(180deg,
    #e8e0d0 0%,
    #d4cbb8 15%,
    #c8bfa8 30%,
    #d0c8b8 50%,
    #b8b0a0 85%,
    #a09080 100%
  );
  border-radius: 25px 25px 15px 15px;
  padding: 20px 22px 45px 22px;
  box-shadow: 0 8px 30px rgba(0, 0, 0, 0.6),
    inset 0 2px 0 rgba(255, 255, 255, 0.4),
    inset 0 -3px 0 rgba(0, 0, 0, 0.15),
    inset 2px 0 0 rgba(255, 255, 255, 0.2),
    inset -2px 0 0 rgba(0, 0, 0, 0.1);
  position: relative;
  max-width: 1100px;
  width: 95%;
}
.monitor-frame::after {
  content: '';
  position: absolute;
  bottom: 0;
  left: 50%;
  transform: translateX(-50%);
  width: 70%;
  height: 12px;
  background: linear-gradient(180deg, #a09080 0%, #888070 100%);
  border-radius: 0 0 8px 8px;
}
.monitor-brand {
  position: absolute;
  bottom: 15px;
  left: 25px;
  font-family: 'Arial Black', Arial, sans-serif;
  font-size: 11px;
  font-weight: 900;
  color: #706858;
  letter-spacing: 1px;
  text-transform: uppercase;
  text-shadow: 1px 1px 0 rgba(255,255,255,0.3);
}
.monitor-controls {
  position: absolute;
  bottom: 13px;
  right: 25px;
  display: flex;
  gap: 8px;
  align-items: center;
}
.power-led {
  width: 6px;
  height: 6px;
  background: #00dd00;
  border-radius: 50%;
  box-shadow: 0 0 6px #00ff00, 0 0 12px #00ff00;
  animation: led-pulse 2s ease-in-out infinite;
}
@keyframes led-pulse {
  0%, 100% {
    opacity: 1;
    box-shadow: 0 0 6px #00ff00, 0 0 12px #00ff00;
  }
  50% {
    opacity: 0.7;
    box-shadow: 0 0 4px #00ff00, 0 0 8px #00ff00;
  }
}
.monitor-button {
  width: 14px;
  height: 14px;
  background: linear-gradient(180deg, #706860 0%, #585048 50%, #484038 100%);
  border-radius: 2px;
  border: 1px solid #383028;
  box-shadow: inset 0 1px 0 rgba(255,255,255,0.2);
}
.screen-bezel {
  background: #0a0a0a;
  border-radius: 12px;
  padding: 8px;
  box-shadow: inset 0 0 50px rgba(0, 0, 0, 0.9),
    inset 0 0 20px rgba(0, 0, 0, 0.8),
    0 2px 4px rgba(0,0,0,0.3);
  border: 3px solid #1a1a1a;
}
.screen-inner {
  border-radius: 8px;
  overflow: hidden;
  position: relative;
  box-shadow: inset 0 0 100px rgba(255,255,255,0.03),
    inset 0 0 30px rgba(0,0,0,0.5);
}
.screen-inner::before {
  content: '';
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  background: radial-gradient(
    ellipse at center,
    transparent 0%,
    transparent 70%,
    rgba(0, 0, 0, 0.15) 100%
  );
  pointer-events: none;
  z-index: 100;
  border-radius: 10px;
}
.crt-screen {
  position: relative;
}
.crt-screen::before {
  content: '';
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  background: repeating-linear-gradient(
    0deg,
    rgba(0, 0, 0, 0.15) 0px,
    rgba(0, 0, 0, 0.15) 1px,
    transparent 1px,
    transparent 2px
  );
  pointer-events: none;
  z-index: 101;
  border-radius: 10px;
}
.crt-screen::after {
  content: '';
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  background: radial-gradient(
    ellipse at center,
    transparent 0%,
    rgba(0, 0, 0, 0.3) 100%
  );
  pointer-events: none;
  z-index: 102;
  border-radius: 10px;
}
.crt-flicker {
  animation: crt-flicker 0.15s infinite;
}
@keyframes crt-flicker {
  0% {
    opacity: 1;
  }
  50% {
    opacity: 0.98;
  }
  100% {
    opacity: 1;
  }
}
.boot-screen {
  min-height: 500px;
  display: flex;
  flex-direction: column;
  justify-content: flex-start;
  padding: 20px;
}
.boot-text {
  white-space: pre-wrap;
  line-height: 1.4;
}
.typing-cursor {
  display: inline-block;
  width: 10px;
  height: 18px;
  background: var(--theme-text);
  animation: blink 0.7s step-end infinite;
  vertical-align: text-bottom;
  margin-left: 2px;
}
.theme-selector {
  display: flex;
  gap: 8px;
}
.theme-btn {
  width: 20px;
  height: 20px;
  border-radius: 50%;
  border: 2px solid #555;
  cursor: pointer;
  transition: transform 0.2s, border-color 0.2s;
}
.theme-btn:hover {
  transform: scale(1.1);
  border-color: #fff;
}
.theme-btn.active {
  border-color: #fff;
  box-shadow: 0 0 10px currentColor;
}
.theme-btn-blue {
  background: #0000aa;
}
.theme-btn-amber {
  background: #ffaa00;
}
.theme-btn-green {
  background: #33ff33;
}
.theme-btn-white {
  background: #ffffff;
}
.terminal-window {
  border-radius: 4px;
}
.terminal-cursor {
  color: #33ff33;
  animation: terminal-blink 0.7s step-end infinite;
}
@keyframes terminal-blink {
  0%, 50% {
    opacity: 1;
  }
  51%, 100% {
    opacity: 0;
  }
}
.terminal-window ::-webkit-scrollbar {
  width: 8px;
}
.terminal-window ::-webkit-scrollbar-track {
  background: #0a0a0a;
}
.terminal-window ::-webkit-scrollbar-thumb {
  background: #33ff33;
  border-radius: 4px;
}
.terminal-window ::-webkit-scrollbar-thumb:hover {
  background: #55ff55;
}
